.label__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.label__input:checked ~ .label__text.label__text--pink {
  color: #ffffff;
  background-color: transparent;
}

.label__input:checked ~ .label__text.label__text--pink:hover {
  border: 1px solid #d3d3d3;
}

.label__input:checked ~ .label__text.label__text--pink:after {
  background-color: #fc5095;
}

.label__input:checked ~ .label__text.label__text--orange {
  color: #ffffff;
  background-color: transparent;
}

.label__input:checked ~ .label__text.label__text--orange:hover {
  border: 1px solid #d3d3d3;
}

.label__input:checked ~ .label__text.label__text--orange:after {
  background-color: #fc8950;
}

.label__input:checked ~ .label__text.label__text--yellow {
  color: #ffffff;
  background-color: transparent;
}

.label__input:checked ~ .label__text.label__text--yellow:hover {
  border: 1px solid #d3d3d3;
}

.label__input:checked ~ .label__text.label__text--yellow:after {
  background-color: #fcc150;
}

.label__input:checked ~ .label__text.label__text--blue {
  color: #ffffff;
  background-color: transparent;
}

.label__input:checked ~ .label__text.label__text--blue:hover {
  border: 1px solid #d3d3d3;
}

.label__input:checked ~ .label__text.label__text--blue:after {
  background-color: #1da7c0;
}

.label__input:checked ~ .label__icon .icon {
