//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START


@font-face {
	@include fontdefault(normal, 200, 'OpenSans');
	@include fontpath('OpenSans-Light', 'OpenSans-Light', 'OpenSans-Light'); }

@font-face {
	@include fontdefault(normal, 400, 'OpenSans');
	@include fontpath('OpenSans-Regular', 'OpenSans-Regular', 'OpenSans-Regular'); }

@font-face {
	@include fontdefault(normal, 600, 'OpenSans');
	@include fontpath('OpenSans-SemiBold', 'OpenSans-SemiBold', 'OpenSans-SemiBold'); }

@font-face {
	@include fontdefault(normal, 700, 'OpenSans');
	@include fontpath('OpenSans-Bold', 'OpenSans-Bold', 'OpenSans-Bold'); }

@font-face {
	@include fontdefault(normal, 300, 'Museo');
	@include fontpath('MuseoSansCyrl-300', 'MuseoSansCyrl-300', 'MuseoSansCyrl-300'); }

@font-face {
	@include fontdefault(normal, 800, 'OpenSans');
	@include fontpath('OpenSans-ExtraBold', 'OpenSans-ExtraBold', 'OpenSans-ExtraBold'); }
